<template>
    <el-table :id="config.id"
              class="table-ins"
              :style="calcStyle"
              :data="config.data"
              :highlight-current-row="config.option.highlightCurrentRow"
              :stripe="config.option.stripe"
              :border="config.option.border"
              :fit="config.option.fit"
              :size="config.option.size"
              :show-summary="config.option.showSummary"
              :sum-text="config.option.sumText"
              :show-header="config.option.showHeader"
              :header-row-style="calcHeaderRowStyle"
              :row-style="calcRowStyle"
              :header-cell-style="calcHeaderCellStyle"
              :cell-style="calcCellStyle"
              @cell-click="onCellClick"
              @row-click="onRowClick">
        <template v-for="(column, index) in config.option.columns">
            <template v-if="column.type =='index'">
                <el-table-column v-if="!column.hidden"
                                 type="index"
                                 :key="index"
                                 :label="column.text"
                                 :width="column.width"
                                 :fixed="column.fixed || false">
                </el-table-column>
            </template>
            <template v-else-if="!column.type || column.type =='text'">
                <el-table-column v-if="!column.hidden"
                                 :key="index"
                                 :prop="column.prop"
                                 :width="column.width"
                                 :sortable="column.sortable"
                                 :fixed="column.fixed || false"
                                 :label="column.text">
                </el-table-column>
            </template>
        </template>
    </el-table>
</template>

<script>
    import DataProxy from '../../../proxy/DataProxy'
    import CONST from '../../../../config/const'
    import _ from 'lodash'

    export default {
        name: 'Table',
        props: ['config'],
        data() {
            return {
                // 
                dataProxy: null,
                // 
                proxyThread: null
            }
        },
        created() {
            this.initStyle()
            this.initOption()
            this.initProxy()
            this.initTransform()
            this.initEvent()
        },
        mounted() {

        },
        beforeDestroy() {
            // 
            this.$store.commit(CONST.MUTATIONS.UNREGISTER, {
                comp: this
            })
        },
        computed: {
            /**
             * 
             */
            calcStyle() {
                return {
                    width: this.config.style.width + 'px',
                    height: this.config.style.height + 'px',
                    opacity: this.config.style.opacity,
                    display: this.config.style.display,
                    backgroundColor: this.config.style.backgroundColor,
                    borderWidth: this.config.style.borderWidth + 'px',
                    borderColor: this.config.style.borderColor,
                }
            },
        },
        methods: {
            /**
             * 
             */
            calcHeaderRowStyle() {
                return {
                    fontSize: this.config.style.header.fontSize + 'px',
                    color: this.config.style.header.color,
                    lineHeight: this.config.style.header.height + 'px',
                    height: this.config.style.header.height + 'px',
                }
            },
            /**
             * 
             */
            calcHeaderCellStyle() {
                return {
                    borderWidth: this.config.style.header.borderWidth + 'px',
                    borderColor: this.config.style.header.borderColor,
                    backgroundColor: this.config.style.header.backgroundColor,
                    paddingLeft: this.config.style.header.paddingLeft + 'px',
                    paddingRight: this.config.style.header.paddingLeft + 'px',
                    paddingTop: this.config.style.header.paddingTop + 'px',
                    paddingBottom: this.config.style.header.paddingTop + 'px',
                }
            },
            /**
             * 
             */
            calcRowStyle(model) {
                return {
                    fontSize: this.config.style.body.fontSize + 'px',
                    color: this.config.style.body.color,
                    lineHeight: this.config.style.body.height + 'px',
                    height: this.config.style.body.height + 'px',
                }
            },
            /**
             * 
             */
            calcCellStyle(model) {
                let bgColor = this.config.style.body.backgroundColor
                if (this.config.option.stripe) {
                    bgColor = model.rowIndex % 2 === 0 ?
                        this.config.style.oddRowBgColor : this.config.style.evenRowBgColor
                }
                return {
                    borderWidth: this.config.style.body.borderWidth + 'px',
                    borderColor: this.config.style.body.borderColor,
                    backgroundColor: bgColor,
                    paddingTop: this.config.style.body.paddingTop + 'px',
                    paddingBottom: this.config.style.body.paddingTop + 'px',
                    paddingLeft: this.config.style.body.paddingLeft + 'px',
                    paddingRight: this.config.style.body.paddingLeft + 'px',
                }
            },
            /**
             * 
             */
            init() {
                this.instanceProxy()
            },
            /**
             * 
             */
            initStyle() {
                let style = _.assignIn({}, {
                    top: 30,
                    left: 30,
                    opacity: 1,
                    display: 'block',
                    borderWidth: 5,
                    oddRowBgColor: '#2d3e55',
                    evenRowBgColor: '#151a23',
                }, this.config.style)
                this.config.style = style
            },
            /**
             * option
             */
            initOption() {
                this.config.option = _.assignIn({}, {
                    border: true,
                    fit: true,
                    stripe: true,
                    size: 'medium',
                    maxHeight: null,
                    highlightCurrentRow: true,
                    showSummary: true,
                    sumText: '',
                    columns: [{
                        type: 'text', text: '', prop: 'name', width: 100, fixed: false, sortable: false
                    }, {
                        type: 'text', text: '', prop: 'value', width: 100, fixed: false, sortable: false
                    }]
                }, this.config.option)
            },
            /**
             * 
             */
            initProxy() {
                let proxy = _.assignIn({}, {
                    // 0-API / 1- / 2-,
                    type: 1,
                    // 
                    autoRequest: false,
                    // 
                    interval: 60,
                    // url
                    url: null,
                    // 
                    param: [{key: null, value: null}],
                }, this.config.proxy)
                this.config.proxy = proxy
            },
            /**
             * proxy
             */
            instanceProxy() {
                this.dataProxy = new DataProxy(this)
            },
            /**
             * transform
             */
            initTransform() {
                let transform = _.assignIn({}, {
                    // 
                    prop: null,
                }, this.config.transform)
                this.config.transform = transform
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'cellClick',
                        comment: '',
                        binders: [
                            {name: 'name', comment: '', prop: 'name', state: null},
                            {name: 'value', comment: '', prop: 'value', state: null},
                        ]
                    }, {
                        name: 'rowClick',
                        comment: '',
                        binders: [
                            {name: 'name', comment: '', prop: 'name', state: null},
                            {name: 'value', comment: '', prop: 'value', state: null},
                        ]
                    }, {
                        name: 'headerClick',
                        comment: '',
                        binders: [
                            {name: 'name', comment: '', prop: 'name', state: null},
                            {name: 'value', comment: '', prop: 'value', state: null},
                        ]
                    },
                ]
            },
            /**
             * 
             */
            render() {
                clearInterval(this.proxyThread)
                this.proxyData()
                if (this.config.proxy.autoRequest && this.config.proxy.interval) {
                    this.proxyThread = setInterval(this.proxyData, this.config.proxy.interval * 1000)
                }
            },
            /**
             * 
             */
            proxyData() {
                this.dataProxy.do()
                    .then((data) => {
                        this.config.data = data
                    })
                    .catch(() => {
                    })
                    .finally(() => {
                        this.$children[0].doLayout()
                    })
            },
            /**
             * 
             */
            commit(stateProp, value) {
                this.$store.commit(CONST.MUTATIONS.MODIFY, {
                    store: stateProp,
                    value: value
                })
            },
            /**
             * 
             */
            reload() {
                this.render()
            },
            /**
             * 
             */
            onEvent(eventName, item, index) {
                if (!this.config.event || !this.config.event.length) {
                    return
                }
                // 
                for (let i = 0; i < this.config.event.length; i++) {
                    let event = this.config.event[i]
                    // 
                    if (event.name != eventName) {
                        continue
                    }
                    // 
                    if (!event.binders || !event.binders.length) {
                        continue
                    }
                    // 
                    for (let j = 0; j < event.binders.length; j++) {
                        let binder = event.binders[j]
                        // 
                        if (!binder.state) {
                            continue
                        }
                        // prop
                        let value
                        // option.data
                        // 
                        if (binder.prop) {
                            value = item[binder.prop]
                        } else {
                            let dataset = this.config.data
                            value = dataset[index][binder.name]
                        }
                        this.commit('screen.design.interaction.' + binder.state, value)
                    }
                }
            },
            /**
             * 
             */
            onCellClick(row, column, cell, event) {
                let index = 0
                for (let i = 0; i < this.config.data.length; i++) {
                    if (this.config.data[i] === row) {
                        index = i
                        break
                    }
                }
                this.onEvent('cellClick', row, index)
            },
            /**
             * 
             */
            onRowClick(row, event, column) {
                let index = 0
                for (let i = 0; i < this.config.data.length; i++) {
                    if (this.config.data[i] === row) {
                        index = i
                        break
                    }
                }
                this.onEvent('rowClick', row, index)

            },
        },
        watch: {
            /**
             * proxy
             */
            'config.proxy': {
                deep: true,
                handler: function () {
                    this.instanceProxy()
                    this.render()
                }
            },
            /**
             * option
             */
            'config.option': {
                deep: true,
                handler: function () {
                    this.$children[0].doLayout()
                }
            },
            /**
             * 
             */
            'config.store': {
                deep: true,
                handler: function () {
                    this.render()
                }
            },
            /**
             * chartoption
             */
            'config.transform': {
                deep: true,
                handler: function () {
                    this.render()
                }
            },
            /**
             * 
             */
            'config.event': {
                deep: true,
                handler: function () {
                    this.initEvent()
                    this.$children[0].doLayout()
                }
            },
        }
    }
</script>
