<template>
    <div class="config-screen">
        <div class="title"></div>
        <el-row>
            <el-col :span="8" class="tag"></el-col>
            <el-col :span="16">
                <el-input v-model="config.name" size="mini"></el-input>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="8" class="tag"></el-col>
            <el-col :span="16">
                <el-input v-model="config.cover"
                          placeholder=""
                          size="mini" clearable></el-input>
                <el-upload drag
                           class="img-upload"
                           :headers="getToken()"
                           :action="getUploadUrl()"
                           :before-upload="beforeUpload"
                           :on-success="onSuccessCover"
                           :on-Error="onError"
                           :show-file-list="false">
                    <img v-if="config.cover"
                         :src="config.cover"
                         class="img-preview">
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text"><em></em></div>
                </el-upload>
                <div v-if="config.cover"
                     class="img-cover">
                    <i class="icon iconfont icon-delete" @click="removeCoverImage()"></i>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="8" class="tag"></el-col>
            <el-col :span="8">
                <el-input-number v-model="config.base.size.width"
                                 size="mini"
                                 controls-position="right"
                                 :min="0">
                </el-input-number>
                <div class="description"></div>
            </el-col>
            <el-col :span="8">
                <el-input-number v-model="config.base.size.height"
                                 size="mini"
                                 controls-position="right"
                                 :min="0">
                </el-input-number>
                <div class="description"></div>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="8" class="tag"></el-col>
            <el-col :span="4">
                <el-color-picker v-model="config.base.backgroundColor"
                                 size="mini" show-alpha></el-color-picker>
            </el-col>
            <el-col :span="4">
                <el-button type="text" size="mini" @click="resetBgColor"></el-button>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="8" class="tag"></el-col>
            <el-col :span="16">
                <el-input v-model="config.base.backgroundImage"
                          placeholder=""
                          size="mini" clearable></el-input>
                <el-upload drag
                           class="img-upload"
                           :headers="getToken()"
                           :action="getUploadUrl()"
                           :before-upload="beforeUpload"
                           :on-success="onSuccess"
                           :on-Error="onError"
                           :show-file-list="false">
                    <img v-if="config.base.backgroundImage"
                         :src="config.base.backgroundImage"
                         class="img-preview">
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text"><em></em></div>
                </el-upload>
                <div v-if="config.base.backgroundImage"
                     class="img-cover">
                    <i class="icon iconfont icon-delete" @click="removeBgImage()"></i>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="8" class="tag"></el-col>
            <el-col :span="16">
                <el-row>
                    <el-radio v-model="config.base.scaleMode" label="0"></el-radio>
                </el-row>
                <el-row>
                    <el-radio v-model="config.base.scaleMode" label="1"></el-radio>
                </el-row>
                <el-row>
                    <el-radio v-model="config.base.scaleMode" label="2"></el-radio>
                </el-row>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import API from '../../../config/api'
    import CONST from '../../../config/const'

    export default {
        name: "Screen",
        props: ["config", "state"],
        data() {
            return {
                default: {
                    // 
                    bgColor: null
                }
            }
        },
        created() {
            this.default = {
                bgColor: this.config.base.backgroundColor
            }
        },
        methods: {
            resetBgColor() {
                this.config.base.backgroundColor = this.default.bgColor
            },
            /**
             * 
             */
            beforeUpload(file) {
                let name = file.name
                let ext = name.substring(name.lastIndexOf(".") + 1).toLowerCase()
                if (ext !== "jpg" && ext !== "png" && ext !== 'bmp' && ext !== 'jpeg') {
                    this.$message({
                        message: "jpgpngbmp",
                        type: "error"
                    })
                    return false
                }
            },
            /**
             * 
             */
            getUploadUrl() {
                return API.ATTACHMENT.UPLOAD
            },
            /**
             * token
             */
            getToken() {
                return {token: this.$store.state.token}
            },
            /**
             * 
             */
            onSuccess(res) {
                this.config.base.backgroundImage = API.ATTACHMENT.DOWNLOAD + "?id=" + res.data.id
            },
            /**
             * 
             */
            onSuccessCover(res) {
                this.config.cover = API.ATTACHMENT.DOWNLOAD + "?id=" + res.data.id
            },
            /**
             * 
             */
            onError(err) {
                this.$message({
                    message: err.data.message,
                    type: "error"
                })
            },
            /**
             * 
             */
            removeBgImage() {
                this.config.base.backgroundImage = null
            },
            /**
             * 
             */
            removeCoverImage() {
                this.config.cover = null
            }
        }
    }
</script>

