<template>
    <el-dialog
            :title="option.title"
            :visible.sync="!style.hidden"
            :width="style.width"
            :fullscreen="style.fullscreen"
            :custom-class="style.class"
            :close-on-click-modal="option.clickToClose !== false"
            :close-on-press-escape="option.escToClose !== false"
            :show-close="option.showCloseBtn !== false"
            :before-close="beforeClose"
            :center="style.center"
            @open="onShow"
            @close="onClose"
            @closed="afterClose">

        <div v-if="option.body.html"
             class="modal-body"
             v-html="option.body.html">
        </div>
        <div v-else class="modal-body">
            <template v-for="(item, index) in option.body.loader">
                <component v-if="!option.body.components[index].config.style
                            || !option.body.components[index].config.style.hidden"
                           :config="option.body.components[index].config"
                           :is="item.default"></component>
            </template>
        </div>

        <span v-if="!option.footer.hidden" slot="footer" class="dialog-footer">
      <template v-if="option.footer.html">
        <div class="dialog-footer-container" v-html="option.footer.html">
        </div>
      </template>
      <template v-else-if="option.footer.components">
        <template v-for="(item, index) in option.footer.loader">
          <component v-if="!option.footer.components[index].config.style
                              || !option.footer.components[index].config.style.hidden"
                     :config="option.footer.components[index].config"
                     :is="item.default"></component>
        </template>
      </template>
      <template v-else>
        <el-button type="primary" @click="style.hidden = true"> </el-button>
        <el-button @click="style.hidden = true"> </el-button>
      </template>
    </span>

    </el-dialog>
</template>

<script>
    import CONST from "../../../../config/const"

    export default {
        name: "Modal",
        props: ['config'],
        data() {
            return {
                id: null,
                style: this.config.style || {hidden: true},
                binder: this.config.binder,
                option: this.config.option,
                handler: this.config.handler,
                event: this.config.event,
                proxy: this.config.proxy,
            }
        },
        created() {
            if (!this.config) {
                throw new Error("Modalconfig")
            }
            this.id = this.config.id || "modal-" + new Date().getTime()
            if (_.get(this.option, "footer.hidden") !== true) {
                _.set(this.option, "footer.hidden", false)
            }
            this.handlerComponent(this.option.body.components, this.option.body.loader)
            this.handlerComponent(this.option.footer.components, this.option.footer.loader)
        },
        mounted() {
            this.init()
        },
        beforeDestroy() {
            // 
            this.$store.commit(CONST.MUTATIONS.SCREEN.UNREGISTER, {
                comp: this
            })
        },
        methods: {

            /**
             * 
             */
            initBinder() {
                for (let key in this.binder) {
                    let prop = this.binder[key][0]
                    let storeProp = this.binder[key][1]
                    this.$store.commit(CONST.MUTATIONS.SCREEN.REGISTER, {
                        store: storeProp,
                        prop: prop,
                        comp: this
                    })
                }
            },
            /**
             * 
             */
            init() {
                this.load()
            },
            /**
             * 
             */
            load() {
                if (this.proxy.remote !== false) {
                    this.loadRemoteData()
                } else {
                    this.transformData()
                    this.proxyMethod(this.event, "onLoad", [this])
                }
            },
            /**
             * 
             */
            loadRemoteData() {
                if (!this.proxy || !this.proxy.remote) {
                    return
                }
                if (!this.proxy.url) {
                    throw new Error("ModalURL")
                }
                if (!this.proxy.method) {
                    this.proxy.method = "POST"
                }
                this.ajax({
                    method: this.proxy.method,
                    url: this.proxy.url,
                    data: this.proxy.param,
                }).then(response => {
                    this.proxy.data = response.data
                    this.transformData()
                })
            },
            /**
             * 
             */
            transformData() {
                if (this.handler && this.handler.transformData) {
                    this.proxyMethod(this.handler, "transformData", [this.proxy.data, this.option, this])
                } else {
                    this.option.data = this.proxy.data
                }
            },
            /**
             * 
             */
            proxyMethod(context, eventName, param) {
                if (!context) {
                    return
                }
                let func = context[eventName]
                if (func && typeof func == "function") {
                    return func.apply(this, param)
                }
            },
            /**
             * binder
             */
            commitBinder(binderKey, value) {
                this.commit(this.binder[binderKey][1], value)
            },
            /**
             * store
             */
            commit(stateProp, value) {
                this.$store.commit(CONST.MUTATIONS.SCREEN.MODIFY, {
                    store: stateProp,
                    value: value
                })
            },
            /**
             * 
             */
            reload() {
                this.load()
            },
            /**
             * 
             */
            onShow() {
                this.proxyMethod(this.event, "onShow", [this])
            },
            /**
             * 
             */
            beforeClose(close) {
                this.proxyMethod(this.event, "beforeClose", [close, this])
            },
            /**
             * 
             */
            onClose() {
                this.proxyMethod(this.event, "onClose", [this])
            },
            /**
             * 
             */
            afterClose() {
                this.proxyMethod(this.event, "afterClose", [this])
            },
            /**
             * 
             */
            show() {
                this.style.hidden = false
            },
            /**
             * 
             */
            hide() {
                this.style.hidden = true
            },
            /**
             * 
             */
            toggle() {
                this.style.hidden = !this.style.hidden
            },
            /**
             * 
             */
            handlerComponent(configs, loadCompArray) {
                if (configs && configs.length) {
                    configs.forEach(config => {
                        if (!config || !config.path || !config.component) {
                            return
                        }
                        loadCompArray.push(require('../../' + config.path + '.vue'))
                    })
                }
            }

        },
        watch: {
            option: {
                deep: true,
                handler: function (val) {
                    this.load()
                }
            },
            event: {
                deep: true,
                handler: function (val) {

                }
            },
            proxy: {
                deep: true,
                handler: function (val) {
                    this.load()
                }
            }
        }
    }
</script>
