import Base from './Base'
import axios from 'axios'
import API from '../../../config/api'
import CONST from '../../../config/const'

class ApiData extends Base {
    constructor(component) {
        super(component)
        let proxy = this.component.config.proxy
        if (!proxy.param || !proxy.param.length) {
            return
        }
        let store = {}
        for (let i = 0; i < proxy.param.length; i++) {
            // 
            let item = proxy.param[i]
            if (!item.key || !item.value) {
                continue
            }
            // vuex
            let vuex = this.component.$root.$store
            // :
            let val = item.value
            if (val.startsWith(':')) {
                let storeProp = val.substring(1)
                if (!storeProp) {
                    continue
                }
                // vuex
                let storePropPath = "screen.design.interaction." + storeProp
                // store
                store[storeProp] = vuex.commit(CONST.MUTATIONS.GET, {prop: storePropPath})
                // 
                vuex.commit(CONST.MUTATIONS.REGISTER, {
                    // vuex
                    store: storePropPath,
                    // 
                    comp: this.component,
                    // vuex
                    prop: "config.store." + storeProp
                })
            }
        }
        this.component.config.store = store
    }

    /**
     * proxy
     * (:)
     */
    do() {
        let proxy = this.component.config.proxy
        if (!proxy || !proxy.url) {
            return new Promise((resolve, reject) => {
            })
        }
        let param = {}
        let vuex = this.component.$root.$store
        if (proxy.param && proxy.param.length) {
            for (let i = 0; i < proxy.param.length; i++) {
                // 
                let item = proxy.param[i]
                if (!item.key) {
                    continue
                }
                let val = item.value
                if (val.startsWith(':')) {
                    let storeProp = val.substring(1)
                    if (!storeProp) {
                        continue
                    }
                    let storePropPath = "screen.design.interaction." + storeProp
                    val = vuex.commit(CONST.MUTATIONS.GET, {prop: storePropPath})
                }
                param[item.key] = val
            }
        }
        if (proxy.remote) {
            return new Promise((resolve, reject) => {
                axios({
                    url: API.DATA_SOURCE.API_PROXY,
                    method: CONST.AJAX.POST,
                    data: {
                        url: proxy.url,
                        method: proxy.method || CONST.AJAX.GET,
                        params: JSON.stringify(param)
                    },
                }).then((response) => {
                    response.data = JSON.parse(response.data)
                    resolve(response)
                }).catch((error) => {
                    reject(error)
                })
            })
        } else {
            // Promise
            return new Promise((resolve, reject) => {
                axios({
                    url: proxy.url,
                    method: proxy.method || CONST.AJAX.GET,
                    data: param,
                }).then((response) => {
                    resolve(response)
                }).catch((error) => {
                    reject(error)
                })
            })
        }
    }
}

export default ApiData
