<template>
    <div class="paint">
        <div class="bg-panel"
             :style="calcBgStyle()">
            <div :style="calcPaintStyle()"
                 style="background-repeat:no-repeat;transform-origin:0 0 0;"
                 class="canvas-panel">
                <template v-if="config.sequence && config.sequence.length > 0">
                    <template v-for="item in config.components">
                        <div :style="calcStyle(item)"
                             :class="config.sequence[state.index] == item.config.id ? 'active' : ''"
                             class="component-box"
                             @click="activeBox($event, item)"
                             @mousedown="dragBox($event, item)">
                            <component :is="loader[item.config.id].default"
                                       :config="config.components[item.config.id].config"></component>
                            <div v-show="config.sequence[state.index] == item.config.id"
                                 :style="calcAuxTop(item)"
                                 class="aux-line aux-top"></div>
                            <div v-show="config.sequence[state.index] == item.config.id"
                                 :style="calcAuxLeft(item)"
                                 class="aux-line aux-left"></div>
                            <div v-show="config.sequence[state.index] == item.config.id"
                                 class="aux-num aux-top">
                                {{item.config.style && item.config.style.top + 'px'}}
                            </div>
                            <div v-show="config.sequence[state.index] == item.config.id"
                                 class="aux-num aux-left">
                                {{item.config.style && item.config.style.left + 'px'}}
                            </div>
                            <div v-show="config.sequence[state.index] == item.config.id"
                                 class="resize resize-tl"
                                 @mousedown="dragCorner($event, item, 'tl')"></div>
                            <div v-show="config.sequence[state.index] == item.config.id"
                                 class="resize resize-tr"
                                 @mousedown="dragCorner($event, item, 'tr')"></div>
                            <div v-show="config.sequence[state.index] == item.config.id"
                                 class="resize resize-bl"
                                 @mousedown="dragCorner($event, item, 'bl')"></div>
                            <div v-show="config.sequence[state.index] == item.config.id" c
                                 class="resize resize-br"
                                 @mousedown="dragCorner($event, item, 'br')"></div>
                            <div v-show="config.sequence[state.index] == item.config.id"
                                 class="resize resize-t"
                                 @mousedown="dragCorner($event, item, 't')"></div>
                            <div v-show="config.sequence[state.index] == item.config.id"
                                 class="resize resize-l"
                                 @mousedown="dragCorner($event, item, 'l')"></div>
                            <div v-show="config.sequence[state.index] == item.config.id"
                                 class="resize resize-r"
                                 @mousedown="dragCorner($event, item, 'r')"></div>
                            <div v-show="config.sequence[state.index] == item.config.id" c
                                 class="resize resize-b"
                                 @mousedown="dragCorner($event, item, 'b')"></div>
                        </div>
                    </template>
                </template>
            </div>
        </div>
        <div class="bottom-bar">
            <div class="scale-bar">
                <span class="icon iconfont icon-minus" @click="scale(-1)"></span>
                <el-slider :show-tooltip="paintScale.showTooltip"
                           v-model="paintScale.index"
                           :min="paintScale.min"
                           :max="paintScale.max"
                           :step="paintScale.step">
                </el-slider>
                <span class="icon iconfont icon-plus" @click="scale(1)"></span>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name: "Paint",
        props: ["config", "state"],
        data() {
            return {
                // 
                loader: {},
                // 
                draggingIndex: null,
                // 
                originalPosition: {x: null, y: null},
                // 
                resizeIndex: null,
                // 
                cornerPosition: null,
                // 
                cornerPoint: {x: null, y: null},
                // 
                paintScale: {
                    showTooltip: true,
                    min: 0.25,
                    max: 2,
                    step: 0.125,
                    index: 1
                },
            }
        },
        created() {
            this.loadComponent()
        },
        methods: {
            /**
             * 
             */
            calcBgStyle() {
                let scale = this.paintScale.index
                return {
                    width: (this.config.base.size.width * scale + 60) + 'px',
                    height: (this.config.base.size.height * scale + 60) + 'px'
                }
            },
            /**
             * 
             */
            calcPaintStyle() {
                return {
                    width: this.config.base.size.width + 'px',
                    height: this.config.base.size.height + 'px',
                    backgroundColor: this.config.base.backgroundColor,
                    backgroundImage: this.config.base.backgroundImage ?
                        'url(' + this.config.base.backgroundImage + ')' : 'none',
                    backgroundSize: this.calcBgImageSize(),
                    transform: 'scale(' + this.paintScale.index + ')'
                }
            },
            /**
             * 
             */
            calcBgImageSize() {
                switch (this.config.base.scaleMode) {
                    case 1:
                        return 'auto 100%'
                    case 2:
                        return '100% 100%'
                    default:
                        return '100% auto'
                }
            },
            /**
             * 
             */
            calcAuxTop(item) {
                if (!item || !item.config || !item.config.style || !item.config.style.top) {
                    return
                }
                return {
                    top: item.config.style.top * -1 + 'px',
                    height: item.config.style.top + 'px',
                }
            },
            /**
             * 
             */
            calcAuxLeft(item) {
                if (!item || !item.config || !item.config.style || !item.config.style.left) {
                    return
                }
                return {
                    left: item.config.style.left * -1 + 'px',
                    width: item.config.style.left + 'px',
                }
            },
            /**
             * 
             */
            activeBox(e, item) {
                this.state.index = this.getIndexById(item.config.id)
                e.stopPropagation()
            },
            /**
             * 
             */
            dragCorner(e, item, position) {
                let index = this.getIndexById(item.config.id)
                // 
                if (e.button === 0) {
                    if (index !== this.state.index) {
                        return
                    }
                    this.cornerPosition = position
                    this.resizeIndex = index
                    // 
                    this.cornerPoint.x = e.screenX
                    this.cornerPoint.y = e.screenY
                    // 
                    window.addEventListener('mousemove', this.resizeBox)
                    window.addEventListener('mouseup', this.endResize)
                }
                if (e.button === 2) {
                    this.showContextMenu(e, index)
                }
                e.stopPropagation()
            },
            /**
             * 
             */
            resizeBox(e, item) {
                // 
                let offsetX = parseInt((e.screenX - this.cornerPoint.x) / this.paintScale.index)
                let offsetY = parseInt((e.screenY - this.cornerPoint.y) / this.paintScale.index)
                // 
                this.cornerPoint.x = e.screenX
                this.cornerPoint.y = e.screenY
                // 
                let id = this.config.sequence[this.state.index]
                let style = this.config.components[id].config.style
                // 
                switch (this.cornerPosition) {
                    case "tl":
                        style.top += offsetY
                        style.left += offsetX
                        style.width -= offsetX
                        style.height -= offsetY
                        break
                    case "tr":
                        style.top += offsetY
                        style.width += offsetX
                        style.height -= offsetY
                        break
                    case "bl":
                        style.left += offsetX
                        style.width -= offsetX
                        style.height += offsetY
                        break
                    case "br":
                        style.width += offsetX
                        style.height += offsetY
                        break
                    case "t":
                        style.top += offsetY
                        style.height -= offsetY
                        break
                    case "l":
                        style.left += offsetX
                        style.width -= offsetX
                        break
                    case "r":
                        style.width += offsetX
                        break
                    case "b":
                        style.height += offsetY
                        break
                    default:
                        return
                }
            },
            /**
             * 
             */
            endResize(e) {
                window.removeEventListener('mousemove', this.resizeBox)
                window.removeEventListener('mouseup', this.endResize)
                e.stopPropagation()
            },
            /**
             * 
             */
            dragBox(e, item) {
                let index = this.getIndexById(item.config.id)
                // 
                if (e.button === 0) {
                    if (index !== this.state.index) {
                        return
                    }
                    this.draggingIndex = index
                    // 
                    this.originalPosition.x = e.screenX
                    this.originalPosition.y = e.screenY
                    // 
                    window.addEventListener('mousemove', this.moveBox)
                    window.addEventListener('mouseup', this.endDrag)
                }
                if (e.button === 2) {
                    this.showContextMenu(e, index)
                }
                e.stopPropagation()
            },
            /**
             * 
             */
            showContextMenu(e, index) {
                this.state.index = index
                this.state.contextMenu.index = index
                this.state.contextMenu.style.left = e.pageX
                this.state.contextMenu.style.top = e.pageY
                window.addEventListener('click', this.hideContextMenu)
            },
            /**
             * 
             */
            hideContextMenu() {
                this.state.contextMenu.index = null
                window.removeEventListener('click', this.hideContextMenu)
            },
            /**
             * 
             */
            moveBox(e) {
                // 
                let offsetX = parseInt((e.screenX - this.originalPosition.x) / this.paintScale.index)
                let offsetY = parseInt((e.screenY - this.originalPosition.y) / this.paintScale.index)
                // 
                this.originalPosition.x = e.screenX
                this.originalPosition.y = e.screenY
                // 
                let id = this.config.sequence[this.state.index]
                let style = this.config.components[id].config.style
                // 
                let x = style.left + offsetX
                let y = style.top + offsetY
                style.left = x
                style.top = y
            },
            /**
             * 
             */
            endDrag(e) {
                this.draggingIndex = null
                window.removeEventListener('mousemove', this.moveBox)
                window.removeEventListener('mouseup', this.endDrag)
                e.stopPropagation()
            },
            /**
             * 
             */
            loadComponent() {
                if (!this.config.sequence || !this.config.sequence.length) {
                    return
                }
                for (let i = 0; i < this.config.sequence.length; i++) {
                    let id = this.config.sequence[i]
                    let comp = this.config.components[id]
                    if (!comp || !comp.name || this.loader[comp.config.id]) {
                        continue
                    }
                    let path = "components/screen/layer/" + comp.category + "/" + comp.name
                    this.loader[comp.config.id] = require("../../" + path + ".vue")
                }
            },
            /**
             * 
             */
            calcStyle(item) {
                let index = this.getIndexById(item.config.id)
                let style = item.config.style
                let result = {
                    zIndex: this.state.index === index ? 999 : this.config.sequence.length - index,
                    top: style && style.top ? style.top + 'px' : 0,
                    left: style && style.left ? style.left + 'px' : 0,
                }
                return result
            },
            /**
             * id
             */
            getIndexById(itemId) {
                for (let i = 0; i < this.config.sequence.length; i++) {
                    let id = this.config.sequence[i]
                    if (id == itemId) {
                        return i
                    }
                }
            },
            /**
             * 
             */
            scale(step) {
                this.paintScale.index += step * this.paintScale.step
            }
        },
        watch: {
            /**
             * 
             */
            "config.components": {
                immediate: true,
                handler: function () {
                    this.loadComponent()
                    // 
                    if (this.config.sequence.length > 0) {
                        this.state.index = this.config.sequence.length - 1
                    }
                }
            }
        }
    }
</script>

