<template>
    <Chart :config="config"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import _ from 'lodash'

    export default {
        name: 'BaseBar',
        props: ['config'],
        data() {
            return {}
        },
        methods: {
            /**
             * 
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'transparent',
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'line',
                            lineStyle: {
                                opacity: 0
                            }
                        },
                        formatter: function (prams) {
                            return prams[0].data
                        }
                    },
                    grid: {
                        left: '0%',
                        right: '0%',
                        bottom: '5%',
                        top: '7%',
                        height: '85%',
                        containLabel: true,
                    },
                    xAxis: [{
                        type: 'category',
                        gridIndex: 0,
                        data: null,
                        axisTick: {
                            alignWithLabel: true
                        },
                        axisLine: {
                            lineStyle: {
                                color: '#0c3b71'
                            }
                        },
                        axisLabel: {
                            show: true,
                            color: 'rgb(170,170,170)',
                            fontSize: 16
                        }
                    }],
                    yAxis: [{
                        type: 'value',
                        gridIndex: 0,
                        splitLine: {
                            show: false
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            lineStyle: {
                                color: '#0c3b71'
                            }
                        },
                        axisLabel: {
                            color: 'rgb(170,170,170)',
                            formatter: '{value}'
                        }
                    }, {
                        type: 'value',
                        splitLine: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        },
                        axisTick: {
                            show: false
                        },
                        axisLabel: {
                            show: false
                        }
                    }],
                    series: [{
                        name: '',
                        type: 'bar',
                        barWidth: '50%',
                        xAxisIndex: 0,
                        yAxisIndex: 0,
                        label: {
                            normal: {
                                show: true,
                                position: 'top',
                                textStyle: {
                                    color: '#ffc72b',
                                    fontSize: 16
                                }
                            }
                        },
                        itemStyle: {
                            normal: {
                                color: '#0286ff'
                            }
                        },
                        data: null,
                        zlevel: 2
                    }, {
                        name: '',
                        type: 'bar',
                        barWidth: '50%',
                        xAxisIndex: 0,
                        yAxisIndex: 1,
                        barGap: '-100%',
                        data: null,
                        itemStyle: {
                            normal: {
                                color: 'rgba(255,255,255,0.1)'
                            }
                        },
                        zlevel: 1
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 
             */
            initData() {
                this.config.data = this.config.data || [
                    {x: '', y: 80},
                    {x: '', y: 87},
                    {x: '', y: 51},
                    {x: '', y: 81},
                    {x: '', y: 23},
                    {x: '', y: 45},
                    {x: '', y: 33}
                ]
            },
            /**
             * 
             * chartparamnamevalue
             *  binder  {name: 'name', comment: '', prop: 'name', state: null} 
             * name: 
             * comment: 
             * prop: paramreturn _.get(param, prop)
             * state: 
             *  this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '',
                        binders: [
                            {name: 'x', comment: '', prop: 'name', state: null},
                            {name: 'y', comment: '', prop: 'data', state: null},
                        ]
                    }
                ]
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        x: null,
                        y: null
                    }
                }
            },
            /**
             * config.dataoption
             */
            handler() {
                let data = this.config.data
                let mapper = this.config.transform.mapper
                let propX = mapper.x || 'x'
                let propY = mapper.y || 'y'
                let xAxis = []
                let series0 = []
                let series1 = []
                for (let i = 0; i < data.length; i++) {
                    xAxis.push(data[i][propX])
                    series0.push(data[i][propY])
                    series1.push(100)
                }
                this.config.option.xAxis[0].data = xAxis
                this.config.option.series[0].data = series0
                this.config.option.series[1].data = series1
            }
        },
        watch: {
            /**
             * 010
             * 10
             */
            'config.option.series': {
                deep: true,
                handler: function (val) {
                    this.config.option.series[1].barWidth = this.config.option.series[0].barWidth
                }
            },
        },
        components: {
            'Chart': Chart
        },
    }
</script>
