<template>
    <div :style="calcStyle"
         class="chart-ins">
        <div :id="config.id"
             :style="{width:config.style.width + 'px', height: config.style.height + 'px'}">
        </div>
    </div>
</template>

<script>
    import echarts from "echarts"
    import DataProxy from "../../../proxy/DataProxy"
    import CONST from "../../../../config/const"
    import _ from "lodash"

    export default {
        name: "Chart",
        props: ['config'],
        data() {
            return {
                // chart
                chart: null,
                // 
                dataProxy: null,
                // 
                proxyThread: null,
                // config.data
                // config.transform
                dataInitCompleted: false
            }
        },
        created() {
            this.initConfig()
            this.initStyle()
        },
        mounted() {
            this.init()
        },
        /**
         * 
         */
        beforeDestroy() {
            this.$store.commit(CONST.MUTATIONS.UNREGISTER, {
                comp: this
            })
        },
        computed: {
            /**
             * 
             */
            calcStyle() {
                return {
                    width: this.config.style.width + 'px',
                    height: this.config.style.height + 'px',
                    opacity: this.config.style.opacity,
                    display: this.config.style.display,
                }
            }
        },
        methods: {
            /**
             * 
             */
            initConfig() {
                if (this.$parent.initOption) {
                    this.$parent.initOption()
                }
                if (this.$parent.initTransform) {
                    this.$parent.initTransform()
                } else {
                    this.initTransform()
                }
                if (this.$parent.initData) {
                    this.$parent.initData()
                }
                if (this.$parent.initEvent) {
                    this.$parent.initEvent()
                }
                if (this.$parent.initProxy) {
                    this.$parent.initProxy()
                } else {
                    this.initProxy()
                }
            },
            /**
             * 
             */
            init() {
                this.initIns()
                this.initEvent()
                this.instanceProxy()
            },
            /**
             * chart
             */
            initIns() {
                let dom = document.getElementById(this.config.id)
                this.chart = echarts.init(dom)
            },
            /**
             * 
             */
            initStyle() {
                this.config.style = this.config.style || {
                    top: 30,
                    left: 30,
                    width: 400,
                    height: 300,
                    opacity: 1,
                    display: 'block',
                }
            },
            /**
             * 
             */
            initProxy() {
                let proxy = _.assignIn({}, {
                    // 0-API / 1- / 2-,
                    type: 1,
                    // 
                    autoRequest: false,
                    // 
                    interval: 60,
                    // url
                    url: null,
                    // 
                    param: [{key: null, value: null}],
                }, this.config.proxy)
                this.config.proxy = proxy
            },
            /**
             * proxy
             */
            instanceProxy() {
                this.dataProxy = new DataProxy(this)
            },
            /**
             * transform
             */
            initTransform() {
                let transform = _.assignIn({}, {
                    // 
                    prop: null,
                }, this.config.transform)
                this.config.transform = transform
            },
            /**
             * 
             */
            initEvent() {
                if (!this.config.event) {
                    return
                }
                let me = this
                // 
                for (let i = 0; i < me.config.event.length; i++) {
                    let event = me.config.event[i]
                    me.chart.on(event.name, function (params) {
                        debugger
                        // 
                        // 
                        if (!event.binders || !event.binders.length) {
                            return
                        }
                        for (let j = 0; j < event.binders.length; j++) {
                            let binder = event.binders[j]
                            if (!binder.state) {
                                continue
                            }
                            let value
                            if (binder.prop) {
                                value = _.get(params, binder.prop)
                            } else {
                                let dataset = me.config.data
                                value = dataset[params.dataIndex][binder.name]
                            }
                            me.commit("screen.design.interaction." + binder.state, value)
                        }
                    })
                }
            },
            /**
             * 
             */
            render() {
                clearInterval(this.proxyThread)
                this.proxyData()
                if (this.config.proxy.autoRequest && this.config.proxy.interval) {
                    this.proxyThread = setInterval(this.proxyData, this.config.proxy.interval * 1000)
                }
            },
            /**
             * 
             */
            proxyData() {
                this.dataProxy.do()
                    .then((data) => {
                        this.config.data = data
                    })
                    .catch(() => {
                    })
                    .finally(() => {
                        this.loadData()
                    })
            },
            /**
             * handlerhandleroption
             * optionhandler
             */
            loadData() {
                if (!this.$parent || !this.$parent.handler) {
                    return
                }
                this.$parent.handler()
            },
            /**
             * chart
             */
            loadOption() {
                try {
                    this.chart.setOption(this.config.option, true)
                } catch (e) {
                    console.log(e)
                }
            },
            /**
             * 
             */
            commit(stateProp, value) {
                this.$store.commit(CONST.MUTATIONS.MODIFY, {
                    store: stateProp,
                    value: value
                })
            },
            /**
             * 
             */
            reload() {
                this.render()
            },
            /**
             * chart
             */
            resize() {
                this.chart.resize({
                    height: this.config.style.height,
                    width: this.config.style.width,
                    silent: true
                })
            }
        },
        watch: {
            "config.style.height": function () {
                this.resize()
            },
            "config.style.width": function () {
                this.resize()
            },
            /**
             * optionoption
             */
            "config.option": {
                deep: true,
                handler: function () {
                    if (!this.dataInitCompleted) {
                        return
                    }
                    this.loadOption()
                }
            },
            /**
             * chartoption
             */
            "config.transform": {
                deep: true,
                handler: function () {
                    if (!this.dataInitCompleted) {
                        return
                    }
                    this.loadData()
                }
            },
            /**
             * datadata
             */
            "config.data": {
                deep: true,
                handler: function () {
                    this.loadData()
                    this.dataInitCompleted = true
                }
            },
            /**
             * proxy
             */
            "config.proxy": {
                deep: true,
                handler: function () {
                    this.instanceProxy()
                    this.render()
                    this.dataInitCompleted = true
                }
            },
            /**
             * 
             */
            "config.store": {
                deep: true,
                handler: function () {
                    if (!this.dataInitCompleted) {
                        return
                    }
                    this.render()
                }
            },
            /**
             * optionchartoption
             */
            "config.handler": {
                deep: true,
                handler: function () {
                    if (!this.dataInitCompleted) {
                        return
                    }
                    this.loadData()
                }
            },
        }
    }
</script>
