<template>
    <div :id="config.id"
         :style="calcStyle"
         class="plugin-ins">
        <slot></slot>
    </div>
</template>

<script>
    import DataProxy from '../../../proxy/DataProxy'
    import CONST from '../../../../config/const'
    import _ from 'lodash'

    export default {
        name: 'Plugin',
        props: ['config'],
        data() {
            return {
                // 
                dataProxy: null,
                // 
                proxyThread: null
            }
        },
        created() {
            this.initStyle()
            this.initProxy()
            this.initTransform()
        },
        mounted() {
            this.init()

        },
        beforeDestroy() {
            this.$store.commit(CONST.MUTATIONS.UNREGISTER, {
                comp: this
            })
        },
        computed: {
            /**
             * 
             */
            calcStyle() {
                return {
                    width: this.config.style.width + 'px',
                    height: this.config.style.height + 'px',
                    opacity: this.config.style.opacity,
                    display: this.config.style.display,
                }
            }
        },
        methods: {
            /**
             * 
             */
            init() {
                this.instanceProxy()
            },
            /**
             * 
             */
            initStyle() {
                let style = _.assignIn({}, {
                    top: 30,
                    left: 30,
                    opacity: 1,
                    display: 'block',
                }, this.config.style)
                this.config.style = style
            },
            /**
             * 
             */
            initProxy() {
                this.config.proxy = _.assignIn({}, {
                    // 0-API / 1- / 2- / null - 
                    type: 1,
                    // 
                    autoRequest: false,
                    // 
                    interval: 60,
                    // url
                    url: null,
                    // 
                    param: [{key: null, value: null}],
                }, this.config.proxy)
            },
            /**
             * proxy
             */
            instanceProxy() {
                this.dataProxy = new DataProxy(this)
            },
            /**
             * transform
             */
            initTransform() {
                let transform = _.assignIn({}, {
                    // 
                    prop: null,
                }, this.config.transform)
                this.config.transform = transform
            },
            /**
             * 
             */
            render() {
                clearInterval(this.proxyThread)
                this.proxyData()
                if (this.config.proxy.autoRequest && this.config.proxy.interval) {
                    this.proxyThread = setInterval(this.proxyData, this.config.proxy.interval * 1000)
                }
            },
            /**
             * 
             */
            proxyData() {
                this.dataProxy.do()
                    .then((data) => {
                        this.config.data = data
                    })
                    .catch(() => {
                    })
                    .finally(() => {
                        this.loadData()
                    })
            },
            /**
             * handlerhandleroption
             * optionhandler
             */
            loadData() {
                if (!this.$parent || !this.$parent.handler) {
                    return
                }
                this.$parent.handler()
            },
            /**
             * 
             */
            commit(stateProp, value) {
                this.$store.commit(CONST.MUTATIONS.MODIFY, {
                    store: stateProp,
                    value: value
                })
            },
            /**
             * 
             */
            reload() {
                this.render()
            },
            /**
             * 
             */
            onEvent(eventName, item, index) {
                if (!this.config.event || !this.config.event.length) {
                    return
                }
                // 
                for (let i = 0; i < this.config.event.length; i++) {
                    let event = this.config.event[i]
                    // 
                    if (event.name != eventName) {
                        continue
                    }
                    // 
                    if (!event.binders || !event.binders.length) {
                        continue
                    }
                    // 
                    for (let j = 0; j < event.binders.length; j++) {
                        let binder = event.binders[j]
                        // 
                        if (!binder.state) {
                            continue
                        }
                        // prop
                        let value
                        // option.data
                        // 
                        if (binder.prop) {
                            value = item[binder.prop]
                        } else {
                            let dataset = this.config.data
                            value = dataset[index][binder.name]
                        }
                        this.commit('screen.design.interaction.' + binder.state, value)
                    }
                }
            },
        },
        watch: {
            /**
             * proxy
             */
            'config.proxy': {
                deep: true,
                handler: function () {
                    this.instanceProxy()
                    this.render()
                }
            },
            /**
             * 
             */
            'config.store': {
                deep: true,
                handler: function () {
                    this.render()
                }
            },
            /**
             * 
             */
            'config.data': {
                deep: true,
                handler: function () {
                    this.loadData()
                }
            },
            /**
             * 
             */
            'config.transform': {
                deep: true,
                handler: function () {
                    this.loadData()
                }
            },
            /**
             * option
             */
            'config.handler': {
                deep: true,
                handler: function () {
                    this.loadData()
                }
            },
        }
    }
</script>
